import equal from 'deep-equal';
figma.showUI(__html__);
function checkEquality(key, nodeValue, masterValue) {
    switch (key) {
        case 'masterComponent':
            console.log('>>>>>>>>>>>>><<<<<<<<<<<<<<');
            console.log('node', nodeValue.name, nodeValue.id);
            console.log('master', masterValue.name, masterValue.id);
            return nodeValue.id === masterValue.id;
        default:
            return equal(nodeValue, masterValue);
    }
}
function getOverrideDataForInstance(node, indent) {
    // console.log("whole node:", node);
    let master = node.masterComponent;
    let nodeData = {
        id: node.id,
        name: node.name,
        overriddenProps: {}
    };
    let logMessage = "| ";
    const dashes = "--".repeat(indent - 1);
    logMessage += dashes + " " + node.name + ' (instance of ' + master.name + ")";
    let props = {};
    overridableProps.forEach(prop => {
        if (prop in node && prop in master && node[prop] !== undefined) {
            if (!checkEquality(prop, node[prop], master[prop])) {
                props[prop] = node[prop];
            }
        }
    });
    logMessage += " => " + Object.keys(props).length + " overrides: ";
    logMessage += Object.keys(props).toString().replace(/,/g, ", ");
    console.log(logMessage, props);
    nodeData.overriddenProps = Object.keys(props).length > 0 ? props : null;
    if (supportsChildren(node) && node.children && node.children.length > 0) {
        let children = [];
        node.children.forEach(child => {
            children.push(getOverrideDataForInstance(child, indent + 1));
        });
        nodeData.children = children.length > 0 ? children : null;
    }
    return nodeData;
}
// Returns type predicate and true if node supports children
function supportsChildren(node) {
    return node.type === "FRAME" || node.type === "COMPONENT" ||
        node.type === "INSTANCE" || node.type === "BOOLEAN_OPERATION";
}
function getOverrideData(selection) {
    let data = {};
    selection.forEach(node => {
        console.log(node);
        if (node.type === "INSTANCE") {
            data[node.id] = getOverrideDataForInstance(node, 1);
        }
        if (node.type === "TEXT") {
            console.log(node.name, 'text node', node, node.characters);
        }
    });
    return data;
}
figma.ui.onmessage = msg => {
    if (msg.type === 'save-props') {
        const selection = Array.from(figma.currentPage.selection);
        let saved = getOverrideData(selection);
        console.log('Done!');
        console.log(saved);
    }
    // figma.closePlugin();
};
